import React from "react";
import "./DonutChart.css";
import { Doughnut } from "react-chartjs-2";

const state = {
  labels: ["Nakul", "Ritika", "Sumeet", "Shubham", "Grejoy"],
  datasets: [
    {
      label: "Messagfes",
      backgroundColor: ["#B21F00", "#C9DE00", "#2FDE00", "#00A6B4", "#6800B4"],
      hoverBackgroundColor: [
        "#501800",
        "#4B5000",
        "#175000",
        "#003350",
        "#35014F",
      ],
      data: [65, 59, 80, 81, 56],
    },
  ],
};
const modData = (chartData) => {
  var newSet = { labels: [], datasets: [{ label:'',backgroundColor:[],hoverBackgroundColor:[],data:[] }] };

  chartData.forEach((element) => {
    newSet.labels.push(element.name);
    newSet.datasets[0].backgroundColor.push(element.color);
    newSet.datasets[0].hoverBackgroundColor.push(element.color);
    newSet.datasets[0].data.push(element.messageCount);
  });
  return newSet;
};

const donut = (props) => (
  <div className="donut">
    <Doughnut
      data={modData(props.chartData)}
      options={{
        title: {
          display: true,
          text: "Text messages sent on  " +props.title+"",
          fontSize: 20,
        },
        legend: {
          display: true,
          position: "bottom",
        },
      }}
    />
  </div>
);
export default donut;
